@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600&display=swap&subset=cyrillic);
.filter{background:white;box-shadow:0px 2px 8px rgba(0,0,0,0.1);border-radius:5px;margin-bottom:20px}.filter-header{font-size:12px;font-weight:600;line-height:12px;letter-spacing:0.5px;text-transform:uppercase;color:#4A4A4A;padding:20px 20px 10px 20px}.filter-content{padding-bottom:10px}

.list-item{font-size:13px;line-height:40px}.list-item-label{display:block;width:100%;cursor:pointer;padding:0 20px;white-space:nowrap;overflow:hidden}.list-item:hover{background:#F1FCFF}.list-item:focus-within{background:#e2f9ff;outline:0}.checkbox{position:relative}.checkbox-input{position:absolute;top:0;left:0;width:100%;height:100%;opacity:0}.checkbox-input:checked+.checkbox-face{border-color:#2196F3}.checkbox-input:checked+.checkbox-face:after{opacity:1}.checkbox-face{position:relative;display:inline-block;width:20px;height:20px;border:1px solid #9abbce;border-radius:2px;margin:0 10px -5px 0}.checkbox-face:after{content:'';display:block;position:absolute;width:12px;height:8px;top:6px;left:3px;background-image:url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg"><path fill="rgb(33, 150, 243)" d="M4.28571 8L0 4.16123L1.20857 3.0787L4.28571 5.82726L10.7914 0L12 1.09021L4.28571 8Z"/></svg>');opacity:0}

.filter-message{background:white;box-shadow:0px 2px 8px rgba(0,0,0,0.1);border-radius:5px;margin-bottom:20px}.filter-message-content{display:flex;justify-content:center;align-items:center;width:100%;height:260px;padding:20px}.filter-message-content .filter-message-title{font-weight:600;font-size:24px;line-height:1.4;text-align:center;color:#4a4a4a}

.sorting{position:relative;color:#4A4A4A;margin-bottom:20px}.sorting-tabs{display:flex;list-style:none;margin:0;padding:0;height:50px}.sorting-tabs-tab{cursor:pointer;display:flex;flex-grow:1;align-items:center;justify-content:center;height:100%;border:1px solid #DFE5EC;background:white}.sorting-tabs-tab.active{background:#2196F3;border:none;color:white}.sorting-tabs-tab:not(.active):hover{background:#F1FCFF}.sorting-tabs-tab:focus{outline:none;box-shadow:0 0 0 2px rgba(33,150,243,0.3)}.sorting-tabs-tab:first-child{border-radius:5px 0 0 5px}.sorting-tabs-tab:last-child{border-radius:0 5px 5px 0}.sorting-tabs-tab .tab-title{font-size:12px;font-weight:600;line-height:20px;letter-spacing:0.5px;text-transform:uppercase}

.page-header{display:flex;justify-content:center;align-items:center;height:160px}.page-header .header-logo-img{max-width:100%}

.ticket{background:white;box-shadow:0px 2px 8px rgba(0,0,0,0.1);border-radius:5px;margin-bottom:20px}.ticket-header{display:flex;width:100%;padding:20px;align-items:center;justify-content:flex-start}.ticket-header .ticket-price{flex-basis:66.66667%;font-weight:600;font-size:24px;line-height:24px;color:#2196F3}.ticket-header .ticket-price:after{content:'\00a0\20BD';font-weight:400}.ticket-header .ticket-carrier{flex-basis:33.33333%;height:36px}.ticket-header .ticket-carrier img{max-height:100%}.ticket-body{padding:0 20px 20px 20px}.ticket-body .segment{display:flex}.ticket-body .segment:not(:last-child){margin-bottom:10px}.ticket-body .segment-content{display:flex;flex-direction:column;flex-basis:33.33333%;min-width:92px}.ticket-body .segment-content:not(:last-child){padding-right:15px}.ticket-body .segment-title{font-weight:600;font-size:12px;line-height:18px;letter-spacing:0.5px;text-transform:uppercase;color:#A0B0B9}.ticket-body .segment-description{font-weight:600;font-size:14px;line-height:21px;color:#4A4A4A}.ticket-body .segment-title,.ticket-body .segment-description{overflow:hidden;white-space:nowrap;text-overflow:ellipsis}.ticket-placeholder{padding:20px}.ticket-placeholder .header:after{content:'';display:block;background-color:white;height:22px}.ticket-placeholder .segment:not(:last-child):after{content:'';display:block;background-color:white;height:16px}

.notify-bar{position:fixed;max-width:860px;width:100%;padding:0 20px;bottom:0;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);z-index:100}.notify-bar .notify{position:relative;background:#fff;min-height:50px;max-width:820px;border-radius:10px;box-shadow:0 3px 10px rgba(0,0,0,0.2);margin:0 auto 15px auto}@media (max-width: 767px){.notify-bar .notify{max-width:320px}}.notify-bar .notify-content{display:flex;align-items:center;justify-content:space-between;padding:5px}@media (max-width: 767px){.notify-bar .notify-content{flex-direction:column;padding:20px}}.notify-bar .notify-content .notify-text{font-size:14px;font-weight:400;line-height:1.5;padding:5px 15px}@media (max-width: 767px){.notify-bar .notify-content .notify-text{text-align:center;margin-bottom:20px;padding:0}}.notify-bar .notify-content .notify-button{cursor:pointer;display:block;background:#2196f3;color:#fff;font-size:14px;font-weight:600;line-height:1.5;text-align:center;min-width:110px;min-height:40px;padding:10px 15px;border:none;flex-shrink:0;border-radius:5px}@media (max-width: 767px){.notify-bar .notify-content .notify-button{width:100%}}.notify-bar .notify-content .notify-button:hover{background:#309df4}.notify-bar .notify-content .notify-button:focus{outline:none;box-shadow:0 0 0 2px rgba(33,150,243,0.3)}.notify-bar .notify.red{background:#ff6663}.notify-bar .notify.red .notify-text{color:#fff}.notify-bar .notify.red .notify-button{color:#ff6663;background:#fff}.notify-bar .notify.red .notify-button:hover{background:#ffe3e3}.notify-bar .notify.red .notify-button:focus{outline:none;box-shadow:0 0 0 2px rgba(255,255,255,0.3)}

.progress-bar{position:fixed;top:0;left:0;width:100%;z-index:1000}

* {
  box-sizing: inherit;
}

html {
  font-size: 10px;
}

body {
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  margin: 0;
  background-color: #F3F7FA;
}
